.skin {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 10px 0 12.5px;
  border-radius: 7.5px;
  background-color: rgb(35, 35, 35);
}

.body {
  width: 82.5%;
  margin: 0;
  padding: 0;
  border: none;
  color: white;
  background-color: transparent;
}

.body:focus {
  outline: none;
}

.body::placeholder {
  color: #9e9e9e;
}

.suffixPane {
  display: flex;
  padding: 0px 7.5px 0 7.5px;
  align-items: center;
  justify-content: center;
}

.buttonEncryptText {
  padding: 0;
  margin: 0;
}
