.mainPane {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background-color: #282627;
}

.bodyPane {
  display: flex;
  width: 1000px;
  height: 600px;
  flex-direction: row;
  border-radius: 10px;
  background-color: rgb(22, 22, 22);
  box-shadow: 0px 0.5px 5px 2px black;
}

.leftPane {
  display: flex;
  flex-grow: 4;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 10px 0 0 10px;
  background-color: white;
}
.leftPane .leftPaneLogo {
  width: 200px;
  height: 200px;
}
.leftPane .leftPaneTitle {
  font-size: 25px;
  font-weight: 100;
  font-family: 'Standard-Font2';
  color: rgb(22, 22, 22);
}
.leftPane .leftPaneSubtitle {
  font-size: 12.5px;
  font-weight: 100;
  font-family: 'Standard-Font2';
  color: rgb(22, 22, 22);
}

.rightPane {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.upperRightPane {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}
.rightPane .upperRightPane .rightPaneTitle {
  font-size: 20px;
  font-weight: 100;
  font-family: 'Standard-Font2';
  color: #f7a35b
}
.rightPane .upperRightPane .rightPaneSubtitle {
  font-size: 15px;
  font-weight: bold;
}
.centerRightPane {
  display: flex;
  flex-grow: 1;
  align-items: center;
  flex-direction: column;
}
.inputSkin {
  width: 67.5%;
  height: 37.5px;
  box-shadow: 0px 0.5px 2px black;
}
.centerRightPane .buttonSkin {
  width: 72.5%;
  height: 37.5px;
  color: black;
  background-color: #f7a35b;
  box-shadow: 0px 0.5px 2px black;
}
.centerRightPane .buttonForgotPassword {
  color: #f7a35b;
  font-weight: bold;
  padding: 0 10px 0 10px;
}

@media screen and (max-width: 500px) {
  .bodyPane {
    height: 100%;
    width: 100%;
  }
  .leftPane {
    display: none;
  }
}
