.mainPane {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background-color: #282627;
}

.bodyPane {
  width: 400px;
  height: 600px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  background-color: rgb(22, 22, 22);
  box-shadow: 0px 0.5px 5px 2px black;
}

.upperBodyPane {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.headerPane {
  width: 72.5%;
}

.headerTitle {
  font-size: 20px;
  font-weight: 100;
  font-family: 'Standard-Font2';
  color: rgba(247, 163, 91);
}

.headerSubtitle {
  font-size: 12.5px;
  font-weight: normal;
}

.centerBodyPane {
  display: flex;
  flex-grow: 1;
  align-items: center;
  flex-direction: column;
}

.logo {
  width: 100px;
  height: 100px;
}

.inputSkin {
  width: 67.5%;
  height: 37.5px;
  box-shadow: 0px 0.5px 2px black;
}

.centerBodyPane .buttonSkin {
  width: 72.5%;
  height: 37.5px;
  color: black;
  background-color: #f7a35b;
  box-shadow: 0px 0.5px 2px black;
}

.centerBodyPane .buttonForgotPassword {
  width: 150px;
  color: #f7a35b;
  font-weight: bold;
}

@media screen and (max-width: 500px) {
  .bodyPane {
    height: 100%;
    width: 100%;
  }
}
